/* Member Block */
.wp-block-udemy-plus-team-member {
    margin-bottom: 1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    --tw-shadow: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
    --tw-shadow-colored: 0 20px 25px -5px var(--tw-shadow-color), 0 8px 10px -6px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  }
  .wp-block-udemy-plus-team-member .author-meta {
    display: flex;
    align-items: center;
  }
  .wp-block-udemy-plus-team-member .author-meta img {
    height: 4rem;
    width: 4rem;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
  .wp-block-udemy-plus-team-member .author-meta img.img-hexagon {
    -webkit-clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
            clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
  }
  .wp-block-udemy-plus-team-member .author-meta img.img-rabbet {
    -webkit-clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
            clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
  }
  .wp-block-udemy-plus-team-member .author-meta img.img-pentagon {
    -webkit-clip-path: polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%);
            clip-path: polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%);
  }
  .wp-block-udemy-plus-team-member .author-meta p {
    margin-left: 0.75rem;
  }
  .wp-block-udemy-plus-team-member .author-meta p strong {
    margin-bottom: 0.25rem;
    display: block;
  }
  .wp-block-udemy-plus-team-member .author-meta p span {
    font-size: 0.75rem;
    line-height: 1rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgb(75 85 99 / var(--tw-text-opacity));
  }
  .wp-block-udemy-plus-team-member .social-links a {
    margin-right: 0.5rem;
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
  
  .wp-block-udemy-plus-team-member .member-bio > p {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  
  /* Editor Styles */
  .wp-block-udemy-plus-team-member .author-meta {
    flex-wrap: wrap;
    position: relative;
  }
  
  .wp-block-udemy-plus-team-member .block-editor-media-placeholder.components-placeholder{
    margin-bottom: 1rem;
  }
  
  .wp-block-udemy-plus-team-member img.is-blob {
    opacity: 0.5;
  }
  
  .wp-block-udemy-plus-team-member .components-spinner {
    position: absolute;
    top: 15px;
    left: 9px;
  }
  
  .wp-block-udemy-plus-team-member .dashicons-plus {
    font-size: 25px;
  }
  
  .social-links > a.is-active {
    background-color: #3858e9;
    color: #fff;
    padding: 4px;
  }
  
  .wp-block-udemy-plus-team-member .team-member-social-edit-ctr {
    margin-top: 1rem;
    padding: 1rem;
    background-color: #efefef;
    border: 1px solid #3858e9;
  }